
.item {
  max-width: 300px;
  overflow: hidden;
  transition-duration: 0.2s;
  /* margin:10px; */
}

.item-neumorphism {
  position:relative;
  border-radius: 12px;
  box-shadow: 5px 5px 10px #abadb0, -5px -5px 10px #ffffff;
  /* z-index:1 */
  background: linear-gradient(145deg, #eaeef1, #c5c8cb);
}

.item-neumorphism.dark {
    background: linear-gradient(145deg, #1a1d21, #16181c);
box-shadow:  5px 5px 10px #0e1013,
             -5px -5px 10px #22262b;
}    
.item-neumorphism:hover {
  border-radius: 5px;
  transform: scale(1.02);
  /* background: linear-gradient(145deg, #c5c8cb, #eaeef1); */
box-shadow:  4px 4px 10px #a9abad,
             -4px -4px 10px #ffffff;
}
.item-neumorphism.dark:hover {
    box-shadow:  5px 5px 10px #0e1013,
             -5px -5px 10px #22262b;
}

.item-neumorphism > .main,
.item-neumorphism > .footer {
  padding: 10px;
}
.item-neumorphism > .main {
  color: var(--light-dark);
  /* max-height:50px; */
  /* overflow: scroll; */
}
.item-neumorphism > .main > p {
  font-size: 14px;
  /* line-height: 0.95 !important; */
}

.item-neumorphism > .main > .title {
  font-size: 15px;
  font-weight: bold;
  /* font-style:italic; */
  text-align: center;

  /* line-height: 0.95 !important; */
}

/* Style the button that is used to open and close the collapsible content */

.divider {
  /* padding:2px;  */
  /* display:block; */
  background-color: var(--light-dark);
  height: 1px;
  width: 20%;
  border-radius: 15px;
}
.description {
  padding: 15px 8px 15px 8px;
  font-size: 12px;
  /* font-weight:bold; */
  font-style: italic;
  /* text-align:center; */
}

