.ThemePickerBackground {
  padding: 13vh 5vh 13vh 5vh;
  display: flex;
  align-items:start;
  justify-content: center;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  /* z-index: 300; */
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  /* animation:fadeInAnimation ease 0.2s; */
  /* animation-iteration-count: 1; */
  /* animation-fill-mode: forwards; */
  transition:opacity 0.2s ease-in-out    
  /* visibility: visible; */
}
.ThemePickerBackground.hide{
    opacity:0;
    pointer-events:none;
    /* z-index: -1; */
    /* visibility:hidden; */
}
@keyframes fadeInAnimation{
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
}
@keyframes fadeOutAnimation{
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
}

.ThemePickerContent {
  background: green;
  opacity: 1;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  letter-spacing: 2px;
  transition-duration: 0.3s;
  border: 4px solid red;
  width: 100vh;
}

.ThemePickerContent.dark {
  background: var(--dark);
  color: var(--light-dark);
  border: 4px solid var(--dark-light);
}

.ThemePickerContent > .search-container {
  background: none;
  min-height: 30px;
  display: flex;
  align-items: center;
  display: flex;
  align-items: center;
  padding: 2vh 3vh 2vh 3vh;
}

.ThemePickerContent > .search-container > i {
  text-align: center;
  /* flex-grow: 1; */
  /* padding: 0 10px 0 10px; */
  font-size: 14px;
  /* font-weight: 700; */
  -webkit-text-stroke: 1px;
  /* max-width: 50px; */
}
.ThemePickerContent > .search-container > .search {
  background: none;
  /* width: 100%; */
  /* min-height: 50px; */
  /* height: 100%; */
  border: none;
  margin-left: 2vh;
  outline: none;
  /* padding: 0 15px 0 5px; */
  box-sizing: border-box;
  font-size: 16px;
  color: inherit;
  letter-spacing: inherit;
  flex-grow: 7;
}
.ThemePickerContent > .theme-list {
  /* max-height: 20%; */
  /* height: 700px; */
  overflow: scroll;
  /* flex-grow: 1; */
  scrollbar-color: var(--light-dark) transparent;
  /* scrollbar-width:thin; */
  /* margin-bottom:auto; */
  /* overflow: -moz-hidden-unscrollable */
}
.ThemePickerContent > .theme-list > .theme-item {
  padding: 1vh 3vh 1vh 3vh;
  transition-duration: 0.2s;
  cursor: pointer;
  user-select: none;
  letter-spacing: 0px;
  font-size: 13px;
}
.ThemePickerContent > .theme-list > .theme-item.selected {
  background: pink;
}
.ThemePickerContent > .theme-list > .theme-item.selected.dark {
  background: var(--light-medium);
  color: var(--dark-light);
}

.theme-item > .icon {
  /* margin: 0 5px 0 0; */
}
.theme-item > .title {
  margin: 0 0 0 7px;
}
